.container {
  padding: 10%;
}
.wrapper {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 15px;
}

.submit {
  padding: 10px 15px;
  background-color: lightgreen;
  border: none;
  border-radius: 0.5em;
  cursor: pointer;
  color:white;
}
.submit:hover{
  background-color: green;
}

.modules {
display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 400px));
}

.modules > a {
  text-decoration: none;
  color: inherit;
  border: lightgray 1px solid;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.card-img {
  width: 100%;
  height: auto;
}
.card-text {
  padding: 10px;
}

