.my-node-enter {
  opacity: 0;
  transform: translateX(200px);
}
.my-node-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 400ms;
}
.my-node-exit {
  opacity: 1;
  transform: translateX(0);
}
.my-node-exit-active {
  opacity: 0;
  transform: translateX(-200px);
  transition: all 400ms;
}